/*!
 * Power Saver - Bluetooth Power Meter Web App
 * Main Stylesheet
 * 
 * Table of Contents:
 * 1. Base Styles & Reset
 * 2. Layout & Grid System  
 * 3. Components (Cards, Buttons, etc.)
 * 4. UI Elements (Menu, Modals, etc.)
 * 5. Responsive Design
 * 6. Animations & Transitions
 * 7. Accessibility & Focus States
 */
* {
  box-sizing: border-box;
}

body {
  color: #fff;
  background: linear-gradient(135deg, #0c0c0c 0%, #1a1a1a 50%, #0c0c0c 100%);
  min-height: 100vh;
  margin: 0;
  padding: 20px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica, Arial, sans-serif;
}

.dashboard {
  grid-template-columns: 1fr;
  gap: 24px;
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
}

.header {
  text-align: center;
  background: linear-gradient(135deg, #1e1e1e 0%, #2a2a2a 100%);
  border: 1px solid #ffffff1a;
  border-radius: 20px;
  padding: 2rem;
  box-shadow: 0 8px 32px #0006;
}

.metrics-grid {
  grid-template-rows: auto auto auto;
  grid-template-columns: 2fr 1fr 1fr;
  gap: 4px;
  display: grid;
}

.metric-card {
  text-align: center;
  background: linear-gradient(135deg, #1e1e1e 0%, #2a2a2a 100%);
  border: 1px solid #ffffff1a;
  border-radius: 20px;
  padding: .5rem;
  position: relative;
  overflow: hidden;
  box-shadow: 0 8px 32px #0006;
}

.metric-card:before {
  content: "";
  background: linear-gradient(90deg, var(--accent-color, #f39c12) 0%, transparent 100%);
  height: 3px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.power-card {
  --accent-color: #f39c12;
  grid-area: 1 / 1 / 3;
}

.hr-card {
  --accent-color: #e74c3c;
  grid-area: 1 / 2;
}

.cadence-card {
  --accent-color: #3498db;
  grid-area: 1 / 3 / 2;
}

.spy-mode-section {
  background: linear-gradient(135deg, #1e1e1e 0%, #2a2a2a 100%);
  border: 1px solid #ffffff1a;
  border-radius: 20px;
  margin-top: 1rem;
  padding: 2rem;
  display: none;
  box-shadow: 0 8px 32px #0006;
}

.spy-instructions {
  color: #999;
  margin-top: .5rem;
  font-size: .7rem;
}

.spy-content {
  justify-content: center;
  display: flex;
}

.spy-card {
  --accent-color: #9b59b6;
  cursor: pointer;
  width: 100%;
  max-width: 400px;
  transition: all .3s;
}

.spy-card:hover {
  border-color: #9b59b666;
  transform: translateY(-2px);
  box-shadow: 0 12px 40px #9b59b64d;
}

.controls-container {
  text-align: center;
  background: linear-gradient(135deg, #1e1e1e 0%, #2a2a2a 100%);
  border: 1px solid #ffffff1a;
  border-radius: 20px;
  padding: 2rem;
  box-shadow: 0 8px 32px #0006;
}

h1 {
  color: #f39c12;
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent;
  background: linear-gradient(45deg, #f39c12, #e67e22) text;
  margin: 0 0 1rem;
  font-size: 2.5rem;
  font-weight: 700;
}

.metric-value {
  color: var(--accent-color, #fff);
  text-shadow: 0 2px 10px #00000080;
  text-align: center;
  margin-bottom: .1rem;
  font-size: clamp(4rem, 8vw, 6rem);
  font-weight: 1000;
  line-height: 1;
}

#power-value {
  color: #f39c12;
  display: inline-block;
  position: relative;
}

.power-card .metric-value {
  letter-spacing: -.02em;
  font-size: clamp(7rem, 18vw, 14rem);
  font-weight: 1000;
}

.metric-label {
  color: #888;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: .5rem;
  font-weight: 600;
  line-height: 1.2;
}

.metric-row {
  justify-content: center;
  align-items: center;
  gap: .8rem;
  display: flex;
}

.metric-info {
  flex-direction: column;
  gap: .1rem;
  display: flex;
}

.metric-unit {
  color: #aaa;
  text-transform: uppercase;
  letter-spacing: .5px;
  font-size: .4rem;
  font-weight: 500;
}

.device-name {
  color: var(--accent-color, #f39c12);
  margin-top: .5rem;
  font-size: .9rem;
  font-weight: 600;
  display: none;
}

.status-text {
  color: #bbb;
  margin-top: 1rem;
  font-size: .9rem;
  font-style: italic;
  display: none;
}

button {
  color: #121212;
  cursor: pointer;
  background: linear-gradient(135deg, #f39c12 0%, #e67e22 100%);
  border: none;
  border-radius: 12px;
  margin: 8px;
  padding: 16px 32px;
  font-size: 16px;
  font-weight: 600;
  transition: all .3s;
  position: relative;
  overflow: hidden;
  box-shadow: 0 4px 16px #f39c124d;
}

button:before {
  content: "";
  background: linear-gradient(90deg, #0000, #fff3, #0000);
  width: 100%;
  height: 100%;
  transition: left .5s;
  position: absolute;
  top: 0;
  left: -100%;
}

button:hover:before {
  left: 100%;
}

button:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 24px #f39c1266;
}

button:disabled {
  color: #888;
  cursor: not-allowed;
  box-shadow: none;
  background: #555;
  transform: translateY(0);
}

button:disabled:before {
  display: none;
}

.metric-connect-button {
  background: linear-gradient(135deg, var(--accent-color, #f39c12) 0%, var(--accent-color, #f39c12) 100%);
  color: #121212;
  cursor: pointer;
  opacity: .9;
  border: none;
  border-radius: 8px;
  width: 100%;
  margin: 8px 0 0;
  padding: 8px 16px;
  font-size: .75rem;
  font-weight: 600;
  transition: all .3s;
  position: relative;
  overflow: hidden;
  box-shadow: 0 2px 8px #0003;
}

.metric-connect-button:before {
  content: "";
  background: linear-gradient(90deg, #0000, #fff3, #0000);
  width: 100%;
  height: 100%;
  transition: left .5s;
  position: absolute;
  top: 0;
  left: -100%;
}

.metric-connect-button:hover:before {
  left: 100%;
}

.metric-connect-button:hover {
  opacity: 1;
  transform: translateY(-1px);
  box-shadow: 0 4px 12px #0000004d;
}

.metric-connect-button:disabled {
  color: #888;
  cursor: not-allowed;
  box-shadow: none;
  opacity: .6;
  background: #555;
  transform: translateY(0);
}

.metric-connect-button:disabled:before {
  display: none;
}

.metric-connect-button .status-text, .metric-connect-button .device-name {
  opacity: .8;
  margin-top: 2px;
  font-size: .6rem;
  line-height: 1.2;
}

.export-buttons {
  flex-wrap: wrap;
  justify-content: center;
  gap: 12px;
  margin-top: 1.5rem;
  display: flex;
}

.export-buttons button {
  background: linear-gradient(135deg, #3498db 0%, #2980b9 100%);
  box-shadow: 0 4px 16px #3498db4d;
}

.export-buttons button:hover {
  box-shadow: 0 8px 24px #3498db66;
}

#exportImageButton {
  font-weight: 600;
  position: relative;
  color: #fff !important;
  background: linear-gradient(135deg, #e67e22 0%, #d35400 100%) !important;
  box-shadow: 0 4px 16px #e67e224d !important;
}

#exportImageButton:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 24px #e67e2266 !important;
}

#exportImageButton:disabled {
  opacity: .6;
  cursor: not-allowed;
  transform: none;
}

.config-notice {
  background: #ffb74d1a;
  border: 1px solid #ffb74d4d;
  border-radius: 8px;
  margin-top: 1rem;
  padding: .75rem;
  font-size: .85rem;
}

.config-notice code {
  white-space: pre-line;
  background: #0006;
  border-radius: 4px;
  margin-top: .5rem;
  padding: .5rem;
  font-family: Courier New, monospace;
  font-size: .8rem;
  line-height: 1.4;
  display: block;
}

#clearSessionButton {
  background: #f44336;
  margin-top: 1rem;
}

.toggle-controls {
  text-align: center;
  margin: 1rem 0;
}

.toggle-button {
  background: linear-gradient(135deg, #9b59b6 0%, #8e44ad 100%);
  justify-content: center;
  align-items: center;
  gap: 8px;
  min-width: 200px;
  padding: 12px 20px;
  font-size: 14px;
  display: inline-flex;
  box-shadow: 0 4px 16px #9b59b64d;
}

.toggle-button:hover {
  background: linear-gradient(135deg, #a569bd 0%, #9b59b6 100%);
  box-shadow: 0 8px 24px #9b59b666;
}

.toggle-icon {
  font-size: 12px;
  transition: transform .3s;
}

.toggle-button.collapsed .toggle-icon {
  transform: rotate(180deg);
}

.section-header {
  border-bottom: 1px solid #ffffff1a;
  margin-bottom: 1.5rem;
  padding-bottom: 1rem;
}

.section-toggle-button {
  background: linear-gradient(135deg, #9b59b6 0%, #8e44ad 100%);
  border-radius: 12px;
  justify-content: center;
  align-items: center;
  gap: 8px;
  width: 100%;
  min-width: 200px;
  padding: 12px 20px;
  font-size: 14px;
  display: inline-flex;
  box-shadow: 0 4px 16px #9b59b64d;
}

.section-toggle-button:hover {
  background: linear-gradient(135deg, #a569bd 0%, #9b59b6 100%);
  box-shadow: 0 8px 24px #9b59b666;
}

.section-toggle-button .toggle-icon {
  font-size: 12px;
  transition: transform .3s;
}

.section-toggle-button.collapsed .toggle-icon {
  transform: rotate(180deg);
}

.bottom-controls {
  flex-direction: column;
  gap: 15px;
  margin-top: 20px;
  display: flex;
}

.hamburger-menu {
  z-index: 1000;
  position: fixed;
  top: 20px;
  right: 20px;
}

.hamburger-button {
  cursor: pointer;
  background: linear-gradient(135deg, #555 0%, #444 100%);
  border: none;
  border-radius: 12px;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  transition: all .3s;
  display: flex;
  box-shadow: 0 4px 16px #5555554d;
}

.hamburger-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 24px #5556;
}

.hamburger-icon {
  flex-direction: column;
  justify-content: space-between;
  width: 20px;
  height: 14px;
  display: flex;
}

.hamburger-line {
  background: #121212;
  border-radius: 1px;
  width: 100%;
  height: 2px;
  transition: all .3s;
}

.hamburger-button.active .hamburger-line:first-child {
  transform: rotate(45deg)translate(5px, 5px);
}

.hamburger-button.active .hamburger-line:nth-child(2) {
  opacity: 0;
}

.hamburger-button.active .hamburger-line:nth-child(3) {
  transform: rotate(-45deg)translate(7px, -6px);
}

.menu-dropdown {
  opacity: 0;
  visibility: hidden;
  background: linear-gradient(135deg, #1e1e1e 0%, #2a2a2a 100%);
  border: 1px solid #ffffff1a;
  border-radius: 12px;
  min-width: 220px;
  transition: all .3s;
  position: absolute;
  top: 60px;
  right: 0;
  overflow: hidden;
  transform: translateY(-10px);
  box-shadow: 0 8px 32px #0006;
}

.menu-dropdown.active {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

.menu-item {
  cursor: pointer;
  border-bottom: 1px solid #ffffff1a;
  justify-content: space-between;
  align-items: center;
  padding: 15px 20px;
  transition: background .3s;
  display: flex;
}

.menu-item:last-child {
  border-bottom: none;
}

.menu-item:hover {
  background: #ffffff0d;
}

.menu-item-text {
  color: #fff;
  font-size: 14px;
  font-weight: 500;
}

.menu-toggle {
  cursor: pointer;
  background: #555;
  border-radius: 10px;
  width: 40px;
  height: 20px;
  transition: background .3s;
  position: relative;
}

.menu-toggle.active {
  background: #f39c12;
}

.menu-toggle-slider {
  background: #fff;
  border-radius: 50%;
  width: 16px;
  height: 16px;
  transition: transform .3s;
  position: absolute;
  top: 2px;
  left: 2px;
}

.menu-toggle.active .menu-toggle-slider {
  transform: translateX(20px);
}

.export-modal {
  z-index: 10000;
  opacity: 0;
  visibility: hidden;
  background: #000c;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  transition: all .3s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.export-modal.show {
  opacity: 1;
  visibility: visible;
}

.export-modal-content {
  background: linear-gradient(135deg, #2a2a2a 0%, #1e1e1e 100%);
  border: 1px solid #ffffff1a;
  border-radius: 16px;
  width: 90%;
  max-width: 500px;
  max-height: 80vh;
  padding: 2rem;
  transition: transform .3s;
  overflow-y: auto;
  transform: scale(.8);
  box-shadow: 0 20px 60px #00000080;
}

.export-modal.show .export-modal-content {
  transform: scale(1);
}

.export-modal-header {
  border-bottom: 1px solid #ffffff1a;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
  padding-bottom: 1rem;
  display: flex;
}

.export-modal-title {
  color: #f39c12;
  margin: 0;
  font-size: 1.5rem;
  font-weight: 600;
}

.export-modal-close {
  color: #999;
  cursor: pointer;
  background: none;
  border: none;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  margin: 0;
  padding: 0;
  font-size: 1.5rem;
  transition: color .3s;
  display: flex;
}

.export-modal-close:hover {
  color: #fff;
}

.export-modal-buttons {
  gap: .75rem;
  margin: 1rem 0;
  display: grid;
}

.export-modal-button {
  color: #fff;
  cursor: pointer;
  text-align: left;
  background: linear-gradient(135deg, #3498db 0%, #2980b9 100%);
  border: none;
  border-radius: 8px;
  align-items: center;
  gap: .5rem;
  padding: .75rem 1rem;
  font-size: .9rem;
  font-weight: 500;
  transition: all .3s;
  display: flex;
}

.export-modal-button:hover {
  transform: translateY(-1px);
  box-shadow: 0 4px 12px #3498db4d;
}

.export-modal-button:disabled {
  color: #888;
  cursor: not-allowed;
  box-shadow: none;
  background: #555;
  transform: none;
}

.export-modal-button.primary {
  background: linear-gradient(135deg, #f39c12 0%, #e67e22 100%);
}

.export-modal-button.primary:hover {
  box-shadow: 0 4px 12px #f39c124d;
}

.export-modal-button.success {
  background: linear-gradient(135deg, #27ae60 0%, #2ecc71 100%);
}

.export-modal-button.success:hover {
  box-shadow: 0 4px 12px #2ecc714d;
}

.export-modal-button.danger {
  background: linear-gradient(135deg, #e74c3c 0%, #c0392b 100%);
}

.export-modal-button.danger:hover {
  box-shadow: 0 4px 12px #e74c3c4d;
}

.export-modal-description {
  color: #ccc;
  margin-bottom: 1rem;
  font-size: .85rem;
  line-height: 1.5;
}

.controls-container.collapsed, .export-section.collapsed, .power-averages-section.collapsed {
  padding: 1rem 2rem;
}

.section-header.collapsed {
  border-bottom: none;
  margin-bottom: 0;
  padding-bottom: 0;
}

.dashboard.has-collapsed-sections {
  grid-template-columns: 1fr;
  gap: 24px;
  display: grid;
}

.collapsed-sections-row {
  flex-wrap: wrap;
  justify-content: center;
  gap: 1rem;
  display: flex;
}

.collapsed-sections-row .controls-container.collapsed, .collapsed-sections-row .export-section.collapsed, .collapsed-sections-row .power-averages-section.collapsed {
  flex: 1;
  min-width: 200px;
  max-width: 300px;
  margin-top: 0;
  padding: 1rem;
}

.collapsed-sections-row .section-toggle-button {
  width: 100%;
  min-width: auto;
  padding: 10px 16px;
  font-size: 12px;
}

.controls-container, .export-section {
  transition: all .4s ease-in-out;
  overflow: hidden;
}

.dashboard {
  transition: all .4s ease-in-out;
}

.power-card .metric-value {
  transition: font-size .4s ease-in-out;
}

.power-card {
  transition: padding .4s ease-in-out;
}

.export-section {
  background: linear-gradient(135deg, #1e1e1e 0%, #2a2a2a 100%);
  border: 1px solid #ffffff1a;
  border-radius: 20px;
  margin-top: 1rem;
  padding: 2rem;
  display: none;
  box-shadow: 0 8px 32px #0006;
}

.export-section .export-buttons {
  margin-top: 0;
}

.power-averages-section {
  background: linear-gradient(135deg, #1e1e1e 0%, #2a2a2a 100%);
  border: 1px solid #ffffff1a;
  border-radius: 20px;
  margin-top: 1rem;
  padding: 2rem;
  display: none;
  box-shadow: 0 8px 32px #0006;
}

.averages-tables {
  flex-direction: column;
  gap: 2rem;
  display: flex;
}

.averages-table-container {
  background: #ffffff0d;
  border: 1px solid #ffffff1a;
  border-radius: 16px;
  padding: 1.5rem;
}

.power-averages-table {
  border-collapse: collapse;
  width: 100%;
  font-size: .9rem;
}

.power-averages-table th {
  color: #f39c12;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: .5px;
  background: #ffffff1a;
  border: 1px solid #fff3;
  padding: .75rem;
  font-size: .8rem;
  font-weight: 600;
}

.power-averages-table td {
  text-align: center;
  color: #fff;
  border: 1px solid #fff3;
  padding: .75rem;
  font-weight: 500;
}

.power-averages-table .row-label {
  color: #aaa;
  text-align: left;
  background: #ffffff0d;
  padding-left: 1rem;
  font-weight: 600;
}

.power-averages-table tbody tr:first-child .row-label {
  color: #3498db;
}

.power-averages-table tbody tr:nth-child(2) .row-label {
  color: #e74c3c;
}

.power-averages-table tbody tr:hover {
  background: #ffffff0d;
}

.dashboard.maximized .power-card .metric-value {
  font-size: clamp(9rem, 22vw, 18rem);
}

.dashboard.maximized .power-card {
  padding: 3rem;
}

.dashboard.maximized .metrics-grid {
  gap: 30px;
}

.metric-box {
  text-align: center;
  background: #ffffff0d;
  border: 1px solid #ffffff1a;
  border-radius: 12px;
  padding: 1rem;
}

.metric-box .value {
  color: #fff;
  margin-bottom: .5rem;
  font-size: 1.8rem;
  font-weight: 700;
}

.metric-box .label {
  color: #aaa;
  text-transform: uppercase;
  letter-spacing: .5px;
  font-size: .8rem;
}

@media (width <= 1024px) {
  .metrics-grid {
    grid-template-rows: auto auto auto;
    grid-template-columns: 1fr 1fr;
  }

  .power-card {
    grid-area: 1 / 1 / auto / 3;
  }

  .hr-card {
    grid-area: 2 / 1;
  }

  .cadence-card {
    grid-area: 2 / 2;
  }

  .speed-card {
    grid-area: 3 / 1;
  }

  .distance-card {
    grid-area: 3 / 2;
  }
}

@media (width <= 768px) {
  body {
    padding: 12px;
  }

  .dashboard {
    gap: 16px;
  }

  .metrics-grid {
    grid-template-rows: repeat(3, auto);
    grid-template-columns: 1fr;
  }

  .power-card, .hr-card, .cadence-card {
    grid-column: 1;
  }

  .power-card {
    grid-row: 1;
  }

  .hr-card {
    grid-row: 2;
  }

  .cadence-card {
    grid-row: 3;
  }

  .header, .metric-card, .controls-container {
    padding: 1.5rem;
  }

  h1 {
    font-size: 2rem;
  }

  .advanced-metrics {
    grid-template-columns: 1fr;
    gap: 12px;
    margin: 1rem 0;
  }
}

@media (width <= 400px) {
  .metrics-grid {
    grid-template-rows: auto auto;
    grid-template-columns: 1fr 1fr;
    gap: 12px;
  }

  .power-card {
    grid-area: 1 / 1 / auto / 3;
  }

  .hr-card {
    grid-area: 2 / 1;
  }

  .cadence-card {
    grid-area: 2 / 2;
  }

  .metric-card {
    padding: .8rem;
  }

  .power-card {
    padding: 1.2rem;
  }
}

@media (width <= 768px) and (width >= 481px) {
  .averages-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }

  .average-card {
    padding: 1rem;
  }

  .average-label {
    margin-bottom: .6rem;
    font-size: .85rem;
  }

  .average-value {
    font-size: 1.2rem;
  }

  .average-title {
    font-size: .75rem;
  }
}

@media (width <= 480px) {
  body {
    padding: 8px;
  }

  .header, .metric-card, .controls-container {
    padding: 1rem;
  }

  h1 {
    margin: 0 0 .5rem;
    font-size: 1.6rem;
  }

  .metric-card {
